



















import Vue from "vue";
import Component from "vue-class-component";

import Modal from "@/core/components/modals/modal.vue";

@Component({
    components: {
        Modal,
    },
})
export default class ConfirmDialog extends Vue {
    $refs!: {
        confirm: HTMLButtonElement;
    };

    visible = false;
    yes = "Yes";
    no = "No";
    title = "";

    resolve = (_ok: boolean) => {};
    reject = () => {};

    confirm() {
        this.resolve(true);
        this.close();
    }
    deny() {
        this.resolve(false);
        this.close();
    }
    close() {
        this.reject();
        this.visible = false;
        this.title = "";
    }
    open(title: string, yes = "yes", no = "no"): Promise<boolean> {
        this.yes = yes;
        this.no = no;
        this.title = title;

        this.visible = true;
        this.$nextTick(() => {
            this.$refs.confirm.focus();
        });

        return new Promise((resolve, reject) => {
            this.resolve = resolve;
            this.reject = reject;
        });
    }
}
