



































import Vue from "vue";
import Component from "vue-class-component";

import Modal from "@/core/components/modals/modal.vue";
import Game from "@/game/game.vue";

import { Note } from "@/game/comm/types/general";
import { gameStore } from "@/game/store";

@Component({
    components: {
        Modal,
    },
})
export default class NoteDialog extends Vue {
    visible = false;
    note: Note | null = null;

    open(note: Note) {
        this.visible = true;
        this.note = note;
    }
    calcHeight() {
        if (this.$refs.textarea) {
            const el = <HTMLElement>this.$refs.textarea;
            el.style.height = "auto";
            el.style.height = el.scrollHeight + "px";
            // Using the return value without the above did not achieve what I want, so hey /shrug
            return el.scrollHeight + "px";
        }
        return "100px";
    }
    updateNote() {
        if (this.note) gameStore.updateNote({ note: this.note, sync: true });
    }
    removeNote() {
        (<Game>this.$parent).$refs.confirm.open("Are you sure you wish to remove this?").then(
            (result: boolean) => {
                if (result && this.note) {
                    gameStore.removeNote({ note: this.note, sync: true });
                    this.visible = false;
                }
            },
            () => {},
        );
    }
}
